body {
  margin: 0;
  font: 18px sans-serif;

  --red: #aaa;
}

.layout {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid: auto / 350px 1fr 250px;
  align-items: flex-start;
  background: #202020;
  color: #cecece;
}

.text-parameters {
  grid: auto / auto;
  display: grid;
}

.text-parameters > div:first-child {
  padding: 15px 20px 5px;
}

.text-parameters > div:not(:first-child):not(:last-child) {
  padding: 5px 20px 5px;
}

.text-parameters > div:last-child {
  padding: 5px 20px 5px;
}

.text-parameters > div:nth-child(even) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.text-input {
  resize: vertical;
  min-height: 65px;
  width: 100%;
  box-sizing: border-box;
  font: inherit;
}

.ff-header {
  display: flex;
  justify-content: space-between;
  align-items: middle;
}

.font-size-input {
  width: 100%;
  margin: 0;
}

.font-style {
  display: grid;
  grid:
    'a b'
    'c b' / 1fr 1fr;
}

.fs-browser-container {
  grid-area: b;
}

.line-height-input {
  width: 100%;
  margin: 0;
}

.canvas {
  border-left: 1px solid rgba(0, 0, 0, 0.4);
  border-right: 1px solid rgba(0, 0, 0, 0.4);
  align-self: stretch;
  overflow: hidden;
  position: relative;
}

.canvas canvas {
  width: 100%;
  height: 100%;
  display: block;
}

.canvas-ui {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  cursor: grab;
}

.canvas-ui.grabbing {
  cursor: grabbing;
}

.canvas-parameters {
  display: grid;
  grid: auto / auto;
}

.canvas-parameters > div:first-child {
  padding: 15px 20px 5px;
}

.canvas-parameters > div:not(:first-child):not(:last-child) {
  padding: 5px 20px 5px;
}

.canvas-parameters > div:last-child {
  padding: 5px 20px 5px;
}
